import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import * as dat from "dat.gui";
import { radToDeg } from "three/src/math/MathUtils.js";

// Scene
const scene = new THREE.Scene();

// Camera (PerspectiveCamera: fov, aspect, near, far)
const camera = new THREE.PerspectiveCamera(
  75,
  window.innerWidth / window.innerHeight,
  0.1,
  1000
);
camera.position.set(0, 1, 5);

const gui = new dat.GUI({ width: 360 });

// Renderer
const renderer = new THREE.WebGLRenderer({ antialias: true });
renderer.setSize(window.innerWidth, window.innerHeight);
renderer.setPixelRatio(window.devicePixelRatio);
document.body.appendChild(renderer.domElement);

// Controls
const controls = new OrbitControls(camera, renderer.domElement);

// Lighting
const ambientLight = new THREE.AmbientLight(0xffffff, 0.6);
scene.add(ambientLight);

const parameters = {};
parameters.count = 100000;
parameters.size = 0.01;
parameters.radius = 5;
parameters.branches = 3;
parameters.spin = 1 
parameters.randomness = 0.2
parameters.randomnessPow = 3
parameters.insideColor = '#e88c0e'
parameters.outsideColor = '#1b3984'

let geometry = null;
let material = null;
let points = null;

//galaxy
const generateGalaxy = () => {
  if (points !== null) {
    geometry.dispose();
    material.dispose();
    scene.remove(points);
  }

  geometry = new THREE.BufferGeometry();
  const positions = new Float32Array(parameters.count * 3);
  const colors = new Float32Array(parameters.count * 3)

  const colorInside = new THREE.Color(parameters.insideColor);
  const outsidecolor = new THREE.Color(parameters.outsideColor);

  colorInside.lerp(outsidecolor, 0.5)

  for (let i = 0; i < parameters.count; i++) {
    const i3 = i * 3;
    const radius = Math.random() * parameters.radius;
    const spinAngle = radius * parameters.spin
    const branchAngle = (i % parameters.branches) / parameters.branches * Math.PI * 2

    const randomX = Math.pow(Math.random(), parameters.randomnessPow)*(Math.random() <0.5 ? 1 : -1)
    const randomY = Math.pow(Math.random(), parameters.randomnessPow)*(Math.random() <0.5 ? 1 : -1)
    const randomZ = Math.pow(Math.random(), parameters.randomnessPow)*(Math.random() <0.5 ? 1 : -1)

  
    positions[i3] =  Math.cos(branchAngle + spinAngle) * radius + randomX
    positions[i3 + 1] = randomY
    positions[i3 + 2] = Math.sin(branchAngle + spinAngle) * radius + randomZ


    const mixColor = colorInside.clone()
    mixColor.lerp(outsidecolor,radius / parameters.radius)
    colors[i3 + 0] = mixColor.r
    colors[i3 + 1] = mixColor.g
    colors[i3 + 2] = mixColor.b
  }
  geometry.setAttribute("position", new THREE.BufferAttribute(positions, 3));
  geometry.setAttribute("color", new THREE.BufferAttribute(colors, 3));


  material = new THREE.PointsMaterial({
    size: parameters.size,
    sizeAttenuation: true,
    depthWrite: false,
    blending: THREE.AdditiveBlending,
    vertexColors: true
  });

  //point
  points = new THREE.Points(geometry, material);
  scene.add(points);
};
generateGalaxy();

gui
  .add(parameters, "count")
  .min(100)
  .max(100000)
  .step(100)
  .onFinishChange(generateGalaxy);
gui
  .add(parameters, "size")
  .min(0.001)
  .max(0.1)
  .step(0.001)
  .onFinishChange(generateGalaxy);
gui
  .add(parameters, "radius")
  .min(0.001)
  .max(20)
  .step(0.01)
  .onFinishChange(generateGalaxy);
  gui
  .add(parameters, "branches")
  .min(2)
  .max(20)
  .step(1)
  .onFinishChange(generateGalaxy);
  gui
  .add(parameters, "spin")
  .min(-5)
  .max(5)
  .step(0.01)
  .onFinishChange(generateGalaxy);

  gui
  .add(parameters, "randomness")
  .min(0)
  .max(2)
  .step(0.001)
  .onFinishChange(generateGalaxy);
  
  gui
  .add(parameters, "randomnessPow")
  .min(1)
  .max(10)
  .step(0.001)
  .onFinishChange(generateGalaxy);

  gui.addColor
  (parameters, "insideColor")
  .onFinishChange(generateGalaxy);
  gui.addColor
  (parameters, "outsideColor")
  .onFinishChange(generateGalaxy);
// Handle window resizing
window.addEventListener("resize", () => {
  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();
  renderer.setSize(window.innerWidth, window.innerHeight);
});

// Animation loop
function animate() {
  requestAnimationFrame(animate);
  controls.update();

  // Render scene
  renderer.render(scene, camera);
}

animate();
